$main_color: #333333;
$color: #444444;
$sub-title: #3BA6C9;
$shadow: #707070;
$border_color: #E5E5E5;
$works__border_color: #DDDDDD;
$box_shadow: #00000029;
$attention: #0C449B;
$bg-section: #EEFBFB;
$bg-faq: #F2F2F2;
$bg-footer: #F8F8F8;
$linearGradient: linear-gradient(to right, #05A5FA, #63FBD7, #67FFD6);
$btn_linearGradient: linear-gradient(to right, #FA41CC, #6020B0);

@mixin width-inner {
  width: 1366px;
  margin: 0 auto;
}

@function lineHeight($fontSize, $lineHeight) {
  @return floor($fontSize / $lineHeight * 10) / 10;
}