html {
  font-size: 62.5%;
}

body {
  color: #333333;
  font-family: "Nato Sans JP", "Hiragino Kaku Gothic ProN", sans-serif;
  font-size: 1.6rem;
  background-color: #fff;
}

.btn {
  width: 155px;
  background: linear-gradient(to right, #FA41CC, #6020B0);
  border-radius: 10px;
  box-shadow: 0 3px 6px #00000029;
}

.header-inner {
  width: 1366px;
  margin: 0 auto;
}

.nav-global {
  width: 1022px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.nav-item {
  display: flex;
  align-items: center;
}
